div.app {
  --zoom-box: 1;
  --zoom-text: 1;
  --vvh: 1024px;
}

div.station {
  font-size: calc(30px * var(--zoom-text));
  width: calc(200px * var(--zoom-box));
  white-space: pre;
  opacity: .8;
  background: #fff;
  border: 1px outset gray;
  position: absolute;
  overflow: hidden;
}

div.station.enlarged {
  width: calc(600px * var(--zoom-box));
  white-space: normal;
  opacity: 1;
}

div.station span.rn {
  color: #fff;
  text-shadow: 0 0 .1vw #000;
  text-align: center;
  width: 1em;
  font-weight: bold;
  display: inline-block;
}

div.station.enlarged span.rn {
  float: left;
  font-size: 190%;
}

div.station span.half {
  vertical-align: bottom;
  background: linear-gradient(to right, #fff, #fff 80%, #777 100%);
  width: calc(50% - .5em);
  display: inline-block;
  overflow: hidden;
}

div.station.enlarged span.half {
  float: right;
  white-space: pre;
  width: calc(100% - 2em);
  overflow: hidden;
}

div.station span.fragile {
  text-align: center;
  border: 1px solid #333;
  width: 50%;
  font-size: 80%;
  display: inline-block;
}

div.station.enlarged span.fragile {
  width: 100%;
}

div.station .hidden {
  display: none;
}

div.station.enlarged span.hidden {
  display: inline;
}

div.station.enlarged div.hidden {
  display: block;
}

div.closebutton {
  float: right;
  background: #c00;
  border: 1px outset #c00;
  font-size: 60%;
}

div.ball {
  width: calc(20px * var(--zoom-text));
  height: calc(20px * var(--zoom-text));
  text-align: center;
  color: #fff;
  font-size: calc(20px * var(--zoom-text));
  text-shadow: 0 0 .1vw #000;
  z-index: -1;
  border-radius: 50%;
  position: absolute;
}

div.Fetcher {
  font-size: calc(20px * var(--zoom-text));
  white-space: pre;
  text-align: center;
  cursor: pointer;
  background: #ccc;
  border: 1px outset gray;
  min-width: 4em;
  padding: .5em 0;
  overflow: hidden;
}

div.Fetcher b {
  color: #fff;
  text-shadow: 0 0 .1vw #000;
  border-radius: 33%;
  padding: .1em .4em;
}

div.FetcherBox {
  z-index: 99999;
  height: var(--vvh);
  background: gray;
  flex-flow: column wrap;
  justify-content: space-around;
  display: flex;
  position: fixed;
}

div.yahball {
  font-size: calc(50px * var(--zoom-text));
  text-align: center;
  background: #7af;
  border-radius: 50%;
  width: -moz-fit-content;
  width: fit-content;
}

